<template>
    <form @submit.prevent="handleSubmit">
      <div v-for="(field, index) in fields" :key="index" class="form-field">
        <label :for="field.name">{{ field.label }}</label>
  
        <input
          v-if="field.type === 'input'"
          :id="field.name"
          v-model="formData[field.name]"
          v-bind="field.attributes"
          :class="{ 'error': errors.includes(field.name) }"
        />
  
        <select
          v-else-if="field.type === 'select'"
          :id="field.name"
          v-model="formData[field.name]"
          :class="{ 'error': errors.includes(field.name) }"
        >
          <option
            v-for="(option, idx) in field.attributes?.options"
            :key="idx"
            :value="option.value"
          >
            {{ option.label }}
          </option>
        </select>
  
        <input
          v-else-if="field.type === 'checkbox'"
          :id="field.name"
          type="checkbox"
          v-model="formData[field.name]"
          :class="{ 'error': errors.includes(field.name) }"
        />
  
        <textarea
          v-else-if="field.type === 'textarea'"
          :id="field.name"
          v-model="formData[field.name]"
          v-bind="field.attributes"
          :class="{ 'error': errors.includes(field.name) }"
        ></textarea>
  
        <slot :name="field.name" :field="field" :value="formData[field.name]"></slot>
      </div>
  
      <button type="submit">Сохранить</button>
      <button type="button" @click="handleCancel">Отмена</button>
  
      <Modal
        v-if="isModalVisible"
        :visible="isModalVisible"
        :message="modalMessage"
        @close="closeModal"
      />
    </form>
  </template>
  
  <script lang="ts">
  import { defineComponent, PropType } from 'vue';
  import Modal from './Modal.vue';
  
  interface Field {
    type: 'input' | 'select' | 'checkbox' | 'textarea';
    label: string;
    name: string;
    attributes?: Record<string, any>;
  }
  
  export default defineComponent({
    components: { Modal },
    props: {
      fields: {
        type: Array as PropType<Field[]>,
        required: true,
      },
      modelValue: {
        type: Object as PropType<Record<string, any>>,
        required: true,
      },
    },
    emits: ['update:modelValue', 'submit', 'cancel'],
    data() {
      return {
        formData: { ...this.modelValue },
        isModalVisible: false,
        modalMessage: '',
        errors: [] as string[],
      };
    },
    watch: {
      modelValue: {
        handler(newVal) {
          this.formData = { ...newVal };
        },
        deep: true,
      },
    },
    methods: {
      handleSubmit() {
        this.errors = [];

        this.fields.forEach((field) => {
          if (
            (field.type === 'input' || field.type === 'textarea') &&
            !this.formData[field.name]
          ) {
            this.errors.push(field.name);
          } else if (
            field.type === 'select' &&
            this.formData[field.name] === undefined
          ) {
            this.errors.push(field.name);
          } else if (
            field.type === 'checkbox' &&
            this.formData[field.name] === false
          ) {
            this.errors.push(field.name);
          }
        });
  
        if (this.errors.length > 0) {
          this.modalMessage = 'Заполните пожалуйста поля';
          this.isModalVisible = true;
          return;
        }
  
        this.$emit('update:modelValue', this.formData);
        this.$emit('submit', this.formData);
        this.modalMessage = 'Спасибо! Ваша форма успешно сохранена';
        this.isModalVisible = true;

        setTimeout(() => {
          this.$router.push('/');
        }, 2000);
      },
      handleCancel() {

        this.formData = {} as Record<string, any>;
        this.$emit('update:modelValue', this.formData);
      },
      closeModal() {
        this.isModalVisible = false; 
      },
    },
  });
  </script>
  
  <style scoped lang="scss">
    .form-field {
      display: flex;
      align-items: center; 
  
    label {
      display: block;
    }
  
    input,
    select,
    textarea {
      width: auto;
      padding: 0.5rem;
      margin-bottom: 0.5rem;
      border: 1px solid #ccc;
      border-radius: 4px;
  
      &.error {
        border-color: red;
      }
    }
  }
  </style>