import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import DemoForm1 from '../views/DemoForm1.vue';
import DemoForm2 from '../views/DemoForm2.vue';
import DemoForm3 from '../views/DemoForm3.vue';
import AboutView from '../views/AboutView.vue'; // Импортируем новую страницу

const routes = [
  { path: '/', component: HomeView },
  { path: '/demo1', component: DemoForm1 },
  { path: '/demo2', component: DemoForm2 },
  { path: '/demo3', component: DemoForm3 },
  { path: '/about', component: AboutView }, // Добавляем маршрут
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;