<template>
    <div class="form-container">
      <h1>Демо форма 3</h1>
      <FormGenerator
        :fields="fields"
        v-model="formData"
        @submit="handleSubmit"
        @cancel="handleCancel"
      />
    </div>
  </template>
  
  <script lang="ts">
  import { defineComponent, ref } from 'vue';
  import FormGenerator from '@/components/FormGenerator.vue';
  
  export default defineComponent({
    components: { FormGenerator },
    setup() {
      const fields = ref([
        { type: 'textarea', label: 'Обратная связь', name: 'feedback', attributes: { placeholder: 'Напишите ваш отзыв' } },
        { type: 'input', label: 'Email', name: 'email', attributes: { placeholder: 'Введите email', type: 'email' } },
        { type: 'checkbox', label: 'Подписаться на рассылку', name: 'subscribe' },
    ]);
  
      const formData = ref({});
  
      const handleSubmit = (data: any) => {
        // Проверка email
        if (!data.email || !data.email.includes('@')) {
          alert('Необходимо правильно указать почту, например: Ivanov@mail.ru');
          return;
        }
  
        console.log('Данные формы:', data);
      };
  
      const handleCancel = () => {
        console.log('Отмена');
      };
  
      return { fields, formData, handleSubmit, handleCancel };
    },
  });
  </script>
  <style scoped lang="scss">
  .form-container {
    max-width: 600px; /* Ограничение ширины */
    margin: 50px auto; /* Центрирование по горизонтали */
    padding: 30px;
    background-color: #ffffff; /* Белый фон */
    border-radius: 10px; /* Закругленные углы */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Тень для эффекта карточки */
    font-family: Arial, sans-serif;
  
    h1 {
      text-align: center;
      margin-bottom: 20px;
      color: #333;
      font-size: 2rem;
      font-weight: bold;
  
      // Анимация появления заголовка
      opacity: 0;
      transform: translateY(-20px);
      animation: fadeInTitle 0.5s ease-in-out forwards;
    }
  
    .form-generator {
      display: flex;
      flex-direction: column;
      align-items: center; /* Центрирование полей */
      gap: 20px; /* Расстояние между элементами */
  
      .form-field {
        width: 100%; /* Поля занимают всю доступную ширину контейнера */
        display: flex;
        flex-direction: column;
  
        label {
          font-weight: bold;
          margin-bottom: 5px;
          color: #555;
        }
  
        input,
        select,
        textarea {
          width: 100%;
          padding: 12px;
          border: 1px solid #ccc;
          border-radius: 5px;
          font-size: 1rem;
          transition: border-color 0.3s ease, box-shadow 0.3s ease;
  
          &:focus {
            border-color: #42b983;
            outline: none;
            box-shadow: 0 0 5px rgba(66, 185, 131, 0.5); /* Эффект фокуса */
          }
        }
  
        // Стиль для чекбокса
        &.checkbox-field {
          display: flex;
          justify-content: flex-end; /* Перемещаем чекбокс вправо */
          align-items: center;
  
          label {
            margin-left: 10px; /* Отступ между чекбоксом и текстом */
          }
  
          input[type="checkbox"] {
            width: auto; /* Убираем фиксированную ширину */
            padding: 0; /* Убираем лишний padding */
          }
        }
  
        textarea {
          resize: vertical; /* Разрешаем изменять высоту текстового поля */
        }
      }
  
      .form-buttons {
        display: flex;
        justify-content: center; /* Кнопки по центру */
        gap: 10px; /* Расстояние между кнопками */
        margin-top: 20px;
  
        button {
          flex: 1; /* Кнопки занимают одинаковое пространство */
          padding: 12px;
          font-size: 1rem;
          border: none;
          border-radius: 5px;
          cursor: pointer;
          transition: background-color 0.3s ease;
  
          &.submit-button {
            background-color: #42b983;
            color: white;
  
            &:hover {
              background-color: darken(#42b983, 10%);
            }
          }
  
          &.cancel-button {
            background-color: #f44336;
            color: white;
  
            &:hover {
              background-color: darken(#f44336, 10%);
            }
          }
        }
      }
    }
  
    // Анимация заголовка
    @keyframes fadeInTitle {
      from {
        opacity: 0; /* Начальная прозрачность */
        transform: translateY(-20px); /* Начальное положение (сверху) */
      }
      to {
        opacity: 1; /* Конечная прозрачность */
        transform: translateY(0); /* Конечное положение (по центру) */
      }
    }
  }
  </style>