<template>
  <div class="about-container">
    <h1 class="fade-in-title">О нас</h1>
    <p
      v-if="isTitleTyped"
      :class="['typing-effect', { 'no-cursor': isMainTextTyped }]"
    >
      {{ mainText }}
    </p>
    <button
      v-if="isMainTextTyped"
      class="fade-in-button"
      @click="goBack"
    >
      Вернуться на главную
    </button>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted, nextTick } from 'vue';
import { useRouter } from 'vue-router';

export default defineComponent({
  name: 'AboutView',
  setup() {
    const router = useRouter();

    // Тексты для анимации
    const mainTextFull =
      'Мы создаем инновационные решения для автоматизации процессов. Наша команда специализируется на разработке современных веб-приложений.';
    const typingSpeed = 50; // Скорость печати (в миллисекундах)

    // Реактивные переменные
    const mainText = ref('');
    const isTitleTyped = ref(false);
    const isMainTextTyped = ref(false);

    // Ссылка на элемент текста
    const mainTextRef = ref<HTMLElement | null>(null);

    // Функция для имитации печати текста
    const typeWriter = (text: string, target: any, callback: () => void) => {
      let index = 0;
      const interval = setInterval(() => {
        if (index < text.length) {
          target.value += text.charAt(index);
          index++;
          updateCursorPosition(target.value);
        } else {
          clearInterval(interval); // Остановить анимацию после завершения
          callback(); // Выполнить коллбэк
        }
      }, typingSpeed);
    };

    // Функция для обновления позиции курсора
    const updateCursorPosition = (currentText: string) => {
      nextTick(() => {
        const targetElement = mainTextRef.value;
        if (targetElement) {
          const range = document.createRange();
          const selection = window.getSelection();
          if (targetElement.childNodes.length > 0) {
            try {
              range.setStart(targetElement.childNodes[0], currentText.length);
              range.collapse(true);
              selection?.removeAllRanges();
              selection?.addRange(range);

              // Позиционируем курсор через CSS
              const rect = range.getBoundingClientRect();
              const cursor = document.querySelector('.typing-effect::after');
              if (cursor instanceof HTMLElement) {
                cursor.style.top = `${rect.top + window.scrollY}px`;
                cursor.style.left = `${rect.right + window.scrollX}px`;
              }
            } catch (error) {
              console.warn('Ошибка при позиционировании курсора:', error);
            }
          }
        }
      });
    };

    // Запуск анимации при монтировании компонента
    onMounted(() => {
      // Заголовок "О нас" появляется сразу через CSS-анимацию
      setTimeout(() => {
        isTitleTyped.value = true; // Разрешаем отображение основного текста

        // Печатаем основной текст
        typeWriter(mainTextFull, mainText, () => {
          isMainTextTyped.value = true; // Разрешаем отображение кнопки
        });
      }, 1000); // Задержка перед началом печатания основного текста
    });

    const goBack = () => {
      router.push('/');
    };

    return {
      mainText,
      isTitleTyped,
      isMainTextTyped,
      goBack,
      mainTextRef,
    };
  },
});
</script>

<style scoped lang="scss">
.about-container {
  display: flex; // Используем Flexbox
  flex-direction: column; // Вертикальное расположение элементов
  justify-content: space-between; // Распределяем элементы по высоте
  align-items: center; // Выравниваем по центру горизонтально
  min-height: 90vh; // Минимальная высота контейнера равна высоте экрана
  max-height: 90vh; // Максимальная высота контейнера равна высоте экрана
  padding: 20px;
  font-family: Arial, sans-serif;
  overflow: hidden; // Запрещаем прокрутку за пределами контейнера

  h1 {
    font-size: 2.5rem;
    color: #333;
    margin-top: 20px; // Отступ сверху для заголовка
    opacity: 0; // Начальная прозрачность
    transform: translateY(-50px); // Начальное положение сверху
    animation: fadeInTitle 1s ease-in-out forwards; // Анимация появления
  }

  button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #42b983;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;

    &:hover {
      background-color: darken(#42b983, 10%);
    }
  }

  p {
    font-size: 2rem;
    color: #666;
    text-align: center;
    max-width: 600px; // Ограничение ширины текста для удобочитаемости
    margin: auto; // Центрируем текст по вертикали
    line-height: 1.5; // Улучшаем читаемость текста
    position: relative; // Для позиционирования курсора
  }

  .typing-effect {
    display: inline-block; // Чтобы курсор был внутри строки
    position: relative;

    &::after {
      content: ''; // Создаем псевдоэлемент для курсора
      position: absolute;
      width: 2px; // Фиксированная ширина курсора
      height: 1.3em; // Высота курсора равна высоте строки
      background-color: #000000; // Цвет курсора
      pointer-events: none; // Курсор не должен мешать взаимодействию
      animation: blink 0.5s step-end infinite; // Мигание курсора
    }
  }

  .no-cursor::after {
    display: none; // Убираем курсор после завершения печати
  }

  @keyframes blink {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
  }

  /* Анимация для заголовка */
  @keyframes fadeInTitle {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

/* Анимация для кнопки */
.fade-in-button {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInButton 0.5s ease-in-out forwards;
  animation-delay: 0.5s;
}

@keyframes fadeInButton {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

</style>